/* ROOT LAYOUT */
.ks-container {
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
}

/* TITLE */
.ks-title {
  font-size: 3rem;
  margin-bottom: 10px;
}

/* HUD */
.hud {
  display: flex;
  justify-content: center;
  gap: 12px;
  margin-bottom: 12px;
}

.stat-box {
  background: #fff2cc;
  border: 2px solid #d1a000;
  border-radius: 8px;
  padding: 6px 10px;
  font-size: 1.1rem;
}

/* CHARACTER IMAGE */
.character-img {
  height: 200px;
  max-height: 32vh;
  width: auto;
  margin: 15px auto;
  display: block;
  image-rendering: high-quality;
  object-fit: contain;
}

/* WORD DISPLAY */
.word-display {
  font-size: 2.8rem;
  letter-spacing: 10px;
  margin-bottom: 16px;
}

/* DEFINITION */
.definition-box {
  background: #ffffffdd;
  border-radius: 10px;
  padding: 10px;
  margin-bottom: 18px;
  font-size: 1.3rem;
  line-height: 1.4;
}

/* LETTER TILE GRID */
.tile-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 14px;
  margin-bottom: 20px;
}

/* LETTER TILES */
.letter-tile {
  width: 65px;
  height: 65px;
  border-radius: 12px;
  background: #fdf5da;
  font-size: 2rem;
  font-weight: bold;
  border: 3px solid #a57c21;
  cursor: pointer;
  transition: transform 0.15s, box-shadow 0.15s;
}

.letter-tile:hover {
  transform: scale(1.12);
}

.letter-tile:active {
  transform: scale(0.92);
}

/* UNDO BUTTON */
.undo-btn {
  margin-top: 12px;
  font-size: 1.4rem;
  padding: 8px 18px;
  border: none;
  background: #c7ebff;
  border-radius: 10px;
  cursor: pointer;
}

/* GAME OVER SCREEN */
.game-over-box {
  background: #fff8db;
  padding: 20px;
  border-radius: 16px;
  font-size: 1.8rem;
}

/* RESPONSIVE ADJUSTMENTS */
@media (max-width: 600px) {
  .character-img {
    height: 150px;
    max-height: 26vh;
  }

  .word-display {
    font-size: 2.2rem;
    letter-spacing: 6px;
  }

  .letter-tile {
    width: 50px;
    height: 50px;
    font-size: 1.6rem;
  }

  .definition-box {
    font-size: 1.1rem;
  }
}


